<template>
  <div class="hello">
        <div class="sidebar">
            <ul class="menu_root">
                <li class="" v-for="(value, key) in menu">
                  <input type="radio" name="menu"/>
                  <a class="sidebar_link" href="javascript:void(0)">{{key}}</a>
                  <ul class="menu_sub">
                    <li v-for="item in value">
                      <router-link v-bind:to="item.e_name">{{ item.name }}</router-link>
                    </li>
                  </ul>
                </li>
            </ul>
        </div>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      menu: {
        HTML: [{
          name: '单复选框组件',
          e_name: 'input',
          show: true
        }, {
          name: 'JavaScript 实现时钟效果',
          e_name: 'js_clock',
          show: false
        }],
        CSS: [{
          name: '2单复选框组件',
          e_name: '2input',
          show: true
        }, {
          name: '2JavaScript 实现时钟效果',
          e_name: '2js_clock',
          show: false
        }],
        JavaScript: [{
          name: '3单复选框组件',
          e_name: '3input',
          show: true
        }, {
          name: '3JavaScript 实现时钟效果',
          e_name: '3js_clock',
          show: false
        }]
      }
    }
  },
  methods () {
    return {
      show: function () {
        console.log(3)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #000;
}
a:hover {
  color: #42b983;
}
.sidebar {
  position: fixed;
  width: 300px;
  height: 100%;
  border-right: 1px solid #333;
  padding: 15px;
  text-align: left;
}
.menu_root > li {
  position: relative;
}
.menu_root > li > .sidebar_link {
  display: block;
  height: 30px;
  line-height: 30px;
  font-size: 20px;
}
input {
  position: absolute;
  top: 0;
  width: 100%;
  height: 30px;
  z-index: 10;
  opacity: 0;
  cursor: pointer;
}
.menu_root .menu_sub {
  display: none;
  padding-left: 1em;
  font-size: 16px;
}
.menu_root > li > input:checked ~ul {
  display: block;
}
.menu_root > li > input:checked ~.sidebar_link {
  color: #42b983;
}



.content {
  width: 800px;
  margin: 0 auto;
}
</style>



